import React, { Component } from "react";
import Title from "../../components/header/title";
import { List, WhiteSpace } from "antd-mobile";
import { Organizelist } from "../../api/index";
import aixinqiye from "../../assets/image/爱心企业.png";
const Item = List.Item;
const Brief = Item.Brief;
class Organize extends Component {
  state = {
    datas: ""
  };
  async componentDidMount() {
    const response = await Organizelist();
    const { data } = response;
    console.log(data.data);
    this.setState({
      datas: data.data
    });
  }
  render() {
    const { datas } = this.state;

    const listdatas = Array.from(datas);
    return (
      <div>
        <Title headtitle="组织详情" back={this.props.history} />
        <List style={{ marginTop: "3rem" }}>
          {listdatas.map((item, index) => {
            return (
              <div key={index}>
                <Item thumb={aixinqiye} onClick={() => {}}>
                  {item.name}

                  <Brief>QQ: {item.qq}</Brief>
                </Item>
                <WhiteSpace size="sm" />
              </div>
            );
          })}
        </List>
      </div>
    );
  }
}
export default Organize;
