import React, {Component} from "react";
import "../../assets/less/main.less";
import {Grid, DatePicker, TextareaItem, Toast} from "antd-mobile";
import Title from "../../components/header/title";
import "../../assets/less/publishreceive.less";
import {publishlist} from '../../redux/action';
import {List, InputItem, WhiteSpace, Button, WingBlank} from "antd-mobile";
import {
  IconFont,
  IconFont2,
  IconFont3,
  IconFont4,
  IconFont5
} from "../../utils/Icon";
import {createForm} from "rc-form";
import moment from 'moment';
import {connect} from 'react-redux'
class Publishreceive extends Component {
  state = {
    hasError: false
  };
  submit = () => {
    this.props.form.validateFields((error, value) => {
      if (!error) {
       // this.props.logins(value);
       value.offtime=JSON.stringify(value.offtime)
        value.time=`${moment().format()}`
        this.props.publishlist(value)
        console.log(value);
      } else {
        console.log("输入错误");
        this.setState({
          hasError: true
        })
      }
    });
  };
  errorcallback = () => {
    Toast.info('请正确输入！', 1);
    this.setState({
      hasError: false
    })
  }
  cancalhandle = () => {
    this.setState({
      hasError: false
    })
  }
  validateDatePicker = (rule, date, callback) => {
    if (date && date.getMinutes() !== 15) {
      callback();
    } else {
      callback(new Error('15 is invalid'));
    }
  }
  render() {
    const {getFieldDecorator} = this.props.form;
    return (
      <div
        style={{
          width: "100vw",
          height: "100vh",
          overflow: "hidden",
          backgroundColor: "#F5F5F9"
        }}
      >
        <Title headtitle="我要发布" back={this.props.history} />
        <div className="form">
          <List>
            {getFieldDecorator("offtime", {
              rules: [
                {required: true, message: "Please input your title"},
                { validator: this.validateDatePicker },
              ]
            })(
              <DatePicker
              
              >
                <List.Item arrow="horizontal">截止时间</List.Item>
              </DatePicker>
            )}
            {getFieldDecorator("title", {
              rules: [
                {required: true, message: "Please input your title"}
              ]
            })(
              <InputItem
                // {...getFieldProps("phone", {onChange(){},rules:[{required:true}]})}
                type="text"
                placeholder="请输入你的活动名称"
                clear
                moneyKeyboardAlign="left"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont5 type="icon-biaoti"></IconFont5>
              </InputItem>
            )}
            <div style={{width: "100vw", height: "1rem", backgroundColor: "#F5F5F9"}}></div>
            {getFieldDecorator("organization", {
              rules: [
                {required: true, message: "Please input your organization"}
              ]
            })(
              <InputItem
                // {...getFieldProps("phone", {onChange(){},rules:[{required:true}]})}
                type="text"
                placeholder="请输入你的组织"
                clear
                moneyKeyboardAlign="left"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont2 type="icon-huiyizuzhi"></IconFont2>
              </InputItem>
            )}
            {getFieldDecorator("person", {
              rules: [{required: true, message: "Please input your person"}]
            })(
              <InputItem
                // {...getFieldProps("password",{onChange(){},rules:[{required:true}]})}
                type="text"
                placeholder="请输入发布者的名字"
                clear
                moneyKeyboardAlign="left"
                maxLength="14"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont3 type="icon-renyuanxinxi"></IconFont3>
              </InputItem>
            )}
            <div style={{width: "100vw", height: "1rem", backgroundColor: "#F5F5F9"}}></div>
            {getFieldDecorator("phone", {
              rules: [{required: true, message: "Please input your phone"}]
            })(
              <InputItem
                // {...getFieldProps("phone", {onChange(){},rules:[{required:true}]})}
                type="phone"
                placeholder="请输入你的手机号码"
                clear
                moneyKeyboardAlign="left"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont type="icon-shouji" size="xxs"></IconFont>
                {/* <MyIcon type="icon-shouji"></MyIcon> */}
              </InputItem>
            )}
            {getFieldDecorator("address", {
              rules: [{required: true, message: "Please input your address"}]
            })(
              <InputItem
                // {...getFieldProps("phone", {onChange(){},rules:[{required:true}]})}
                type="text"
                placeholder="请输入你的地址"
                clear
                moneyKeyboardAlign="left"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont4 type="icon-dizhi"></IconFont4>
              </InputItem>
            )}
            {getFieldDecorator("introduce", {
              rules: [{required: true, message: "Please input your introduce"}]
            })(
              <TextareaItem
                title="活动描述"
                placeholder="活动的简单描述"
                data-seed="logId"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              />
            )}
            <div style={{width: "100vw", height: "0.5rem", backgroundColor: "#F5F5F9"}}></div>
            <Button type="primary" onClick={this.submit}>
              发布
            </Button>
          </List>
        </div>
      </div>
    );
  }
}
const Formpublish = createForm()(Publishreceive);
export default connect(state => {
  console.log(state)
  const {publish} = state;
  const {code, message} = publish;
  if (code === 1) {
    Toast.success("发布成功", 1);
  }
  if (message && code === 0) {
    Toast.fail("发布失败", 1);
  }
  return {
    status: state
  }
}, {publishlist})(Formpublish);
