import React, { Component } from "react";
import { Layout, Menu, Icon, Input, Table, Divider, Tag,Modal, Button } from "antd";
import "../../../assets/less/pcmain.less";
import { Pcmaindata } from "../../../api/index";
import { IconFont6, IconFont7 } from "../../../utils/Icon";
import {Redirect} from 'react-router-dom';
import pclogin from '../PC_login/pclogin';
const columns = [
  {
    title: "发布组织",
    dataIndex: "organization",
    key: "name",
    render: text => <a>{text}</a>
  },
  {
    title: "组织负责人",
    dataIndex: "person",
    key: "person"
  },
  {
    title: "活动标题",
    key: "title",
    dataIndex: "title"
  },
  {
    title: "活动地点",
    dataIndex: "address",
    key: "address"
  },
  {
    title: "详细介绍",
    key: "introduce",
    dataIndex: "introduce"
  },
  {
    title: "发布时间",
    key: "pulishtime",
    dataIndex: "pulishtime"
  },
  {
    title: "联系电话",
    key: "telephone",
    dataIndex: "telephone"
  }
];
const columns1 = [
  {
    title: "账号",
    dataIndex: "phone",
    key: "phone",
    render: text => <a>{text}</a>
  },
  {
    title: "信誉积分",
    key: "credit",
    dataIndex: "credit"
  },
  {
    title: "志愿时间",
    key: "volunteertime",
    dataIndex: "volunteertime"
  },
  {
    title: "昵称",
    key: "nickname",
    dataIndex: "nickname"
  }
];
const columns2 = [
  {
    title: "账号",
    dataIndex: "phone",
    key: "phone",
    render: text => <a>{text}</a>
  },
  {
    title: "信誉积分",
    key: "credit",
    dataIndex: "credit"
  },
  {
    title: "昵称",
    key: "nickname",
    dataIndex: "nickname"
  }
];
class Pcmain extends Component {
  state = {
    data1: [],
    data2: [],
    data3: [],
    address:'',
    visible: false,
   
  };
  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    }); 
    this.props.history.replace('/pclogin');
    
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  handleClick=()=>{
    this.setState({
      visible: true,
    });
   
  }
  async handlclick(e) {
    const ajax = { address: e };
    const response = await Pcmaindata(ajax);
    const { data } = response.data;
    if (e === "users") {
      this.setState({
        address:'users',
        data1:data
      })
    } else if (e === "pulishusers") {
      this.setState({
        address:'pulishusers',
        data2:data
      })
    } else if (e === "activity") {
      // const  passdata=Array.from(data);
      this.setState({
        address:'activity',
        data3: data
      });
    }
  }
   async componentDidMount()
   {
    const ajax = { address: 'users' };
    const response = await Pcmaindata(ajax);
    const { data } = response.data;
    this.setState({
      address:'users',
      data1:data
    })
   }
  render() {
    
    return (
      <div style={{ width: "100vw", height: "100vh", overflow: "hidden" }}>
         <Modal
          title="是否退出?"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          style={{textAlign:"center"}}
          okText="确认"
          cancelText="取消"
        >
         
        </Modal>
        <div className="pcheader">
          <div className="pctitle">聚益志愿服务系统</div>
          <div className="pcadmin">
            <IconFont6 type="icon-renyuanguanli"></IconFont6>admin
          </div>
          <div className="pcback" onClick={this.handleClick}>
            <IconFont7 type="icon-tuichu"></IconFont7>退出
          </div>
        </div>
        <div className="pccontent">
          <div className="pcslider">
            <Menu
              style={{ marginTop: "2rem" }}
              defaultSelectedKeys={["volunteer"]}
            >
              <Menu.Item
                key="volunteer"
                onClick={this.handlclick.bind(this, "users")}
              >
                <Icon type="heart" />
                志愿者
              </Menu.Item>
              <Menu.Item
                key="publisher"
                onClick={this.handlclick.bind(this, "pulishusers")}
              >
                <Icon type="user" />
                发布者
              </Menu.Item>
              <Menu.Item
                key="activity"
                onClick={this.handlclick.bind(this, "activity")}
              >
                <Icon type="gift" />
                志愿活动
              </Menu.Item>
            </Menu>
          </div>
          <div className="pcdisplay">
            <div className="pcdisplay-title">志愿者</div>
            <div className="pcdisplay-search"></div>
            {
                ((address,data1,data2,data3)=>{
                 if(address==="users")
                 {
                   return(
                    <Table
                    columns={columns1}
                    dataSource={data1}
                    pagination={{ pageSize: 50 }}
                    scroll={{ y: 240 }}
                  />
                   )
                 }
                 else if(address==="pulishusers")
                 {
                   return(
                    <Table
                    columns={columns2}
                    dataSource={data2}
                    pagination={{ pageSize: 50 }}
                    scroll={{ y: 240 }}
                  />
                   )
                 }
                 else if(address==="activity")
                 {
                   return(
                      <Table
                         columns={columns}
                         dataSource={data3}
                         pagination={{ pageSize: 50 }}
                         scroll={{ y: 240 }}
                      />
                   )
                 }
                })(this.state.address,this.state.data1,this.state.data2,this.state.data3)

            }
           
          </div>
        </div>
        <div className="pcfoot">Copyright @ 2020东北林业大学版权所有</div>
      </div>
    );
  }
}
export default Pcmain;
