import React, { Component } from 'react';
import Title from '../../components/header/title';
import Introduce from '../../components/header/introduce/introduce';
import '../../assets/less/sign.less';
import dianzan from '../../assets/image/点赞.png';
import dianzan1 from '../../assets/image/点赞 (1).png';
import {Button} from 'antd-mobile';
import {Toast} from 'antd-mobile';
import {connect} from 'react-redux';
import {Listsignup} from '../../api/index';
class Sign extends Component{
    state={
      flag:false
    }
    update=()=>{
       this.setState({
           flag:!this.state.flag
       })
    }
    async handlesign(index)
    {
        console.log(this.props)
        const phone=this.props.listmessage.user.user.phone
        const response=await Listsignup({activity_id:index,phone});
        console.log(response.data);
        if(response.data.code===1)
        {
            Toast.success('报名成功', 1);
        }
        else{
            Toast.fail('报名失败', 1);
        }
        console.log(index)
    }
    render()
    {

        //console.log(this.props.listmessage.list)
        return(
            <div style={{overflowX:'hidden',position:"relative",height:"100vh"}}>das
                 <Title headtitle="活动详情" back={this.props.history} />
                 <Introduce  listmessage={this.props.listmessage.list} />
                 <div className="sign-up">
                  <img onClick={this.update} src={this.state.flag ? dianzan1:dianzan} alt=""/>
                  <Button className="sign-button" type="primary" onClick={this.handlesign.bind(this,this.props.listmessage.list.activity_id)}>我要报名</Button>
                 </div>
            </div>
        )
    }
}
export default connect(state=>{
    console.log(state)
    return {
        listmessage:state
    }
},{})(Sign);