import React, { Component } from "react";
import { SearchBar, List } from "antd-mobile";
import "../../assets/less/search.less";
import { Activelish } from "../../api/index";
import { sign_up } from "../../redux/action";
import { connect } from "react-redux";
import back from "../../assets/image/返回.png";
const Item = List.Item;
let result;
class Search extends Component {
  state = {
    data: "",
    newdata: ""
  };
  async componentDidMount() {
    const response = await Activelish();
    const { data } = response;
    result = data.data;
    this.setState(() => ({
      data: result
    }));
  }
  handleClick(item) {
    //console.log(item)
    this.props.sign_up(item);
    this.props.history.push("/sign");
  }
  handlechange(e) {
   
    const { data } = this.state;
    const datas = data.filter(item => {
      if (e) {
        if (item.title.indexOf(e) != -1) {
          return item;
        }
      }
    });
    this.setState({
      newdata: datas
    });
  }
  render() {
    const { newdata } = this.state;
    return (
      <div>
        <SearchBar
          placeholder="搜索活动"
          ref={ref => (this.autoFocusInst = ref)}
          onChange={this.handlechange.bind(this)}
        />

        <List>
          {(newdata => {
            if (newdata.length != 0) {
              console.log(newdata);
              for (let i = 0; i < newdata.length; i++) {
                return (
                  <Item
                    arrow="horizontal"
                    multipleLine
                    onClick={this.handleClick.bind(this, newdata[i])}
                    platform="android"
                  >
                    {newdata[i].title}
                  </Item>
                );
              }
            } else {
              return (
                <div
                  style={{
                    textAlign: "center"
                  }}
                >
                  没有相关内容
                </div>
              );
            }
          })(newdata)}
        </List>
      </div>
    );
  }
}
export default connect(
  () => {
    return {};
  },
  { sign_up }
)(Search);
