import React, {Component} from 'react';
import Title from '../../components/header/title'
import {Button, InputItem, List, Toast} from 'antd-mobile'
import {createForm} from "rc-form";
import {IconFont8, IconFont9, IconFont10} from '../../utils/Icon'
import { Radio } from 'antd';
import {connect} from 'react-redux';
import {informationlist,informationlist1} from '../../redux/action'
import { Redirect } from "react-router-dom";
class Information extends Component {
    state = {
        hasError: false,
        value: 1
    };
    onChange = e => {
        console.log('radio checked', e.target.value);
        this.setState({
          value: e.target.value,
        });
      };
    
    submit = () => {
        this.props.form.validateFields((error, value) => {
            if (!error) {
                if(value.experience==0||value.experience===1)
                {
                    //
                    value.phone=this.props.user.user.user.phone
                    //console.log(value)
                    this.props.informationlist(value)
                }
                else{
                    Toast.fail("请输入完善信息", 1);
                }
            } else {
                console.log("输入错误");
                this.setState({
                    hasError: true
                })
            }
        });
    };
    errorcallback = () => {
        Toast.info('请正确输入！', 1);
        this.setState({
            hasError: false
        })
    }
    cancalhandle = () => {
        this.setState({
            hasError: false
        })
    }
    componentDidMount()
    {
        var infor={};
        infor.phone=this.props.user.user.user.phone;
        this.props.informationlist1(infor)
    }
    render() {
        console.log(this.props)
       const { user } = this.props.user;
       const { code} = user;
       
       if (code === 1) {
          return <Redirect  to='/'></Redirect>
       }
       else if(this.props.user.infor.code===2)
       {
        return <Redirect  to='/'></Redirect>
       }
        const {getFieldDecorator} = this.props.form;
        return (
            <div
                style={{
                    width: "100vw",
                    height: "100vh",
                    overflow: "hidden",
                    backgroundColor: "#F5F5F9"
                }}
            >
                <Title headtitle="基本信息" back={this.props.history} />
                <div className="form">
                    <List>
                        {getFieldDecorator("nickname", {
                            rules: [
                                {required: true, message: "Please input your title"}
                            ]
                        })(
                            <InputItem
                                // {...getFieldProps("phone", {onChange(){},rules:[{required:true}]})}
                                type="text"
                                placeholder="请输入你的昵称"
                                clear
                                moneyKeyboardAlign="left"
                                error={this.state.hasError}
                                onErrorClick={this.errorcallback}
                                onFocus={this.cancalhandle}
                            >
                                <IconFont8 type="icon-nicheng"></IconFont8>
                            </InputItem>
                        )}
                         <div style={{width: "100vw", height: "1.5rem", backgroundColor: "#F5F5F9"}}></div>
                        {getFieldDecorator("special", {
                            rules: [
                                {required: true, message: "Please input your special"}
                            ]
                        })(
                            <InputItem
                                // {...getFieldProps("major", {onChange(){},rules:[{required:true}]})}
                                type="text"
                                placeholder="请输入你的特长"
                                clear
                                moneyKeyboardAlign="left"
                                error={this.state.hasError}
                                onErrorClick={this.errorcallback}
                                onFocus={this.cancalhandle}
                            >
                                <IconFont10 type="icon-techang"></IconFont10>
                            </InputItem>
                        )}
                        <div style={{width: "100vw", height: "1.5rem", backgroundColor: "#F5F5F9"}}></div>
                        {getFieldDecorator("major", {
                            rules: [
                                {required: true, message: "Please input your title"}
                            ]
                        })(
                            <InputItem
                                // {...getFieldProps("major", {onChange(){},rules:[{required:true}]})}
                                type="text"
                                placeholder="请输入你的专业"
                                clear
                                moneyKeyboardAlign="left"
                                error={this.state.hasError}
                                onErrorClick={this.errorcallback}
                                onFocus={this.cancalhandle}
                            >
                                <IconFont9 type="icon-zhuanye"></IconFont9>
                            </InputItem>
                        )}
                        <div style={{width: "100vw", height: "1.5rem", backgroundColor: "#F5F5F9"}}></div>
                        {getFieldDecorator("experience", {
                            rules: [
                                {required: false, message: "Please choose"}
                            ]
                        })(

                            <Radio.Group      
                            name="radiogroup" style={{height:"3rem",lineHeight:"3rem"}}>
                            <Radio value={1}>有志愿活动的经验</Radio>
                            <Radio value={0}>没有志愿活动的经验</Radio>
                           
                          </Radio.Group>
                        )}
                        <div style={{width: "100vw", height: "3rem", backgroundColor: "#F5F5F9"}}></div>
                        <Button type="primary" onClick={this.submit}>
                            发布
                </Button>
                    </List>
                </div>
            </div>
        )
    }
}
const FormInformation = createForm()(Information);
export default connect(
    state =>{
        const { user } = state
        const {code,msg}=user;
        if (code === 1) {
          Toast.success("信息填入成功 !!!", 1);
        }
        if (msg && code === 0) {
          Toast.fail("信息填写失败 !!!", 1);
        }
        
        //console.log(state)
        return {user:state}
      },
{informationlist,informationlist1}
)(FormInformation)