import React, { Component } from "react";
import "../../assets/less/title.less";
import fanhui from "../../assets/image/返回.png";
class Title extends Component {
    back=()=>{
        this.props.back.goBack();
    }
  render() {
    return (
      <div className="title3">
        <img className="back" src={fanhui} alt="" onClick={this.back} />
        {/* 在react路由中非常重要*/}
        <div className="center">{this.props.headtitle}</div>
      </div>
    );
  }
}
export default Title;
