import React, { Component } from 'react';
import Title from '../../components/header/title'
class Ward extends Component{
    render()
    {
        return(
            <div>
              <Title headtitle="奖励" back={this.props.history} />
              <div style={{marginTop:'4rem'}}>当我们完成志愿的时候，会有相应的组织提供相应的奖励，请耐心等待。。。。。</div>
               
            </div>
        )
    }
}
export default Ward;
