import React, { Component } from "react";
import { List, InputItem, Button, Toast } from "antd-mobile";
import { createForm } from "rc-form";
import Head from "../../components/header/header";
import { WingBlank, WhiteSpace } from "antd-mobile";
import "../../assets/less/index.less";
import { IconFont, IconFont1 } from "../../utils/Icon";
import { connect } from "react-redux";
import { logins } from "../../redux/action";
import { Redirect } from "react-router-dom";
class login extends Component {
  state = {
    hasError: false
  };
  submit =  () => {
    this.props.form.validateFields((error, value) => {
      if (!error) {
        this.props.logins(value);

        //console.log(this.props.user);
      } else {
        console.log("输入错误");
        this.setState({
          hasError:true
        })
      }
    });
  };
  errorcallback=()=>{
    Toast.info('请正确输入！',1);
    this.setState({
      hasError:false
    })
  }
  cancalhandle=()=>{
    this.setState({
      hasError:false
    })
  }
  registerhandle=()=>{
    this.props.history.replace("/register");
  }
  render() {                  
    const {  getFieldDecorator } = this.props.form;
   
    const { user } = this.props.user;
    const { status, Redirecto } = user;
    
    if (status === 1) {
       return <Redirect  to='/information'></Redirect>
    }
    return (
      <div>
        <Head />
        <WingBlank size="lg">
          <List>
            <WhiteSpace size="lg"></WhiteSpace>
            {getFieldDecorator("phone", {
              rules: [{ required: true, message: "Please input your phone" }]
            })(
              <InputItem
                // {...getFieldProps("phone", {onChange(){},rules:[{required:true}]})}
                type="phone"
                placeholder="请输入你的手机号码"
                clear
                moneyKeyboardAlign="left"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont type="icon-shouji" size="xxs"></IconFont>
                {/* <MyIcon type="icon-shouji"></MyIcon> */}
              </InputItem>
            )}

            <WhiteSpace size="lg"></WhiteSpace>
            {getFieldDecorator("password", {
              rules: [{ required: true, message: "Please input your passwoed" }]
            })(
              <InputItem
                // {...getFieldProps("password",{onChange(){},rules:[{required:true}]})}
                type="password"
                placeholder="请输入你的密码"
                clear
                moneyKeyboardAlign="left"
                maxLength="14"
                error={this.state.hasError}
                onErrorClick={this.errorcallback}
                onFocus={this.cancalhandle}
              >
                <IconFont1 type="icon-icon2"></IconFont1>
              </InputItem>
            )}

            <WhiteSpace size="lg"></WhiteSpace>
            <Button type="primary" onClick={this.submit}>
              登录
            </Button>
          </List>
        </WingBlank>
        <footer className="foot" style={{marginTop:'1rem'}}>
          <ul>
            <li>帮助？</li>
            <li onClick={this.registerhandle}>注册</li>
          </ul>
        </footer>
      </div>
    );
  }
}
const FormLogin = createForm()(login);
export default connect(
  state =>{
    const { user } = state
    const {status,msg}=user;
    if (status === 1) {
      Toast.success("Load success !!!", 1);
    }
    if (msg && status === 0) {
      Toast.fail("Load failed !!!", 1);
    }
    //console.log(state)
    return {user:state}
  },
  { logins }
)(FormLogin);
