import React, { Component } from "react";
import { List } from "antd-mobile";
import "../../../assets/less/sign.less";
import heart from "../../../assets/image/heart.png";
import lianxiren from "../../../assets/image/联系人.png";
import dianhua from "../../../assets/image/电话.png";
import address1 from "../../../assets/image/地址.png";
const Item = List.Item;
const Brief = Item.Brief;
class Introduce extends Component {
  render() {
   
    const { address,introduce ,organization,person,subtitle,telephone,title} = this.props.listmessage;
    return (
      <div style={{ marginTop: "2rem" }} className="introduceback">
        <div className="introducetitle">
          &nbsp;&nbsp;{subtitle}
    <div className="subtitle">{title}</div>
        </div>
        <div className="introduce-item">
          <img className="introduceicon" src={heart} alt="" />
          <ul>
            <li className="organizetion">组织</li>
    <li>{organization}</li>
          </ul>
        </div>
        <div className="introduce-item">
          <img className="introduceicon" src={lianxiren} alt="" />
          <ul>
            <li className="organizetion">联系人</li>
  <li>{person}</li>
          </ul>
        </div>
        <div className="introduce-item">
          <img className="introduceicon" src={dianhua} alt="" />
          <ul>
            <li className="organizetion">电话</li>
    <li>{telephone}</li>
          </ul>
        </div>
        <div className="introduce-item">
          <img className="introduceicon" src={address1} alt="" />
          <ul>
            <li className="organizetion">地址</li>
            <li>{address}</li>
          </ul>
        </div>
        <div className="introducecenter">
          <span
            style={{ fontSize: "1.5rem", marginTop: "1rem", display: "block" }}
          >
            详情介绍
          </span>
          <br />
          <div
            style={{
              textIndent: "1.5rem",
              textAlign: "left",
              lineHeight: "1.6rem",
              marginTop: "0.5rem",
              fontSize: "1rem"
            }}
          >
           {introduce}
          </div>
        </div>
      </div>
    );
  }
}
export default Introduce;
