import React, { Component } from "react";
import Login from "../login/login";
import Register from "../register/register";
import Main from "../Main/main";
import List from '../List/list';
import Sign from '../sign/sign';
import ReceiveList from '../recivelist/receivelist';
import Person from '../../containers/person/person';
import Search from '../../containers/search/search';
import Organize from '../../containers/organize/orangize';
import Credit from '../../containers/credit/credit';
import Heart from '../../containers/heart/heart';
import Welfare from '../../containers/welfare/welfare';
import  Pulishlogin from '../../containers/pulishlogin/pulishlogin'
import Pulishregister from '../../containers/pulishregister/pulishregister';
import Pulishmain from '../../containers/Pulishmain/pulishmain';
import Publishreceive from '../../containers/publishreceive/publishreceive';
import Publishlist from '../../containers/publishlist/publishlist';
import Publishperson from '../../containers/publishperson/publishperson';
import Pclogin from '../../containers/PC/PC_login/pclogin';
import Pcmain from '../../containers/PC/PC_mian/pcmain';
import Information from '../../containers/Information/Information';
import Ward  from '../../containers/ward/ward';
import Help  from '../../containers/Help/help';
import Set  from '../../containers/Set/set';
import { HashRouter as Router, Route, Switch } from "react-router-dom";
class App extends Component {
  render() {
    return (
      <Router>
        <Route path="/" exact component={Main} />
        <Route path="/login" exact component={Login} />
        <Route path="/register" exact component={Register} />
        <Route path="/list" exact component={List} />
        <Route path="/sign" exact component={Sign} />
        <Route path="/receive" exact component={ReceiveList} />
        <Route path="/person" exact component={Person} />
        <Route path="/search" exact component={Search} />
        <Route path="/organize" exact component={Organize} />
        <Route path="/credit" exact component={Credit} />
        <Route path="/heart" exact component={Heart} />
        <Route path="/welfare" exact component={Welfare} />
        <Route path="/pulishlogin" exact component={Pulishlogin} />
        <Route path="/pulishregister" exact component={Pulishregister}/>
        <Route path="/pulishmain" exact component={Pulishmain}/>
        <Route path="/publishreceive" exact component={Publishreceive}/>
        <Route path="/publishlist" exact component={Publishlist}/>
        <Route path="/publishperson" exact component={Publishperson}/>
        <Route path="/pclogin" exact component={Pclogin}/>
        <Route path="/pcmain" exact component={Pcmain}/>
        <Route path="/information" exact component={Information}/>
        <Route path="/ward" exact component={Ward}/>
        <Route path="/help" exact component={Help}/>
        <Route path="/set" exact component={Set}/>
      </Router>
    );
  }
}
export default App;
