import {Login,Register,Pulisherlogin,Pulisherregister,Published,Pclogin,Infor,Infor1} from '../api/index'
import {SuCCESS_MESSAGE,FAIL_MESSAGE,SUCCESS_REGISTER,LIST_CHOOSE,PUBLISG_SUCCESS,PUBLISH_FAIL,LOGIN_SUCCESS,LOGIN_FAIL,
INFOR_FAIL,INFOR_SUCCESS,INFOR_FAIL1,INFOR_SUCCESS1
} from './actionType'





//创建action
//登录成功
const loginSuccess=status=>({type:SuCCESS_MESSAGE,data:status});
//失败
const errorMessage=status=>({type:FAIL_MESSAGE,data:status})
//注册成功
const registerSuccess=status=>({type:SUCCESS_REGISTER,data:status})
//报名选项
const listSelect=count=>({type:LIST_CHOOSE,data:count})
//发布活动
const publishedSuccess=status=>({type:PUBLISG_SUCCESS,data:status})
//发布活动失败
const publishederror=status=>({type:PUBLISH_FAIL,data:status})
//pc登录成功
const Loginsuccess=status=>({type:LOGIN_SUCCESS,data:status})
//pc登录失败
const Loginfail=status=>({type:LOGIN_FAIL,data:status})
// 志愿者注入信息
const Inforsuccess=status=>({type:INFOR_SUCCESS,data:status})
//
const Inforfail=status=>({type:INFOR_FAIL,data:status})

const Inforsuccess1=status=>({type:INFOR_SUCCESS1,data:status})
//
const Inforfail1=status=>({type:INFOR_FAIL1,data:status})



// 异步action
export const logins=user=>{
    return async dispatch=>{
        const response=await Login(user);
        const {code}=response.data;
        const {data}=response;
        if(code===1)
        {
          //登录成功，有该用户
          dispatch(loginSuccess(data))
          //console.log(data)
        }
        else
        {
          //登录失败
          dispatch(errorMessage(data));
          //console.log(data)
        }
    }
}

