export const register=user=>{
    return async dispatch=>{
        const response=await Register(user);
        const {code}=response.data;
        const {data}=response;
        if(code===2)
        {
            dispatch(registerSuccess(data));
        }
        else{
            dispatch(errorMessage(data))
        }
    }
}

