//报名事件分发

export const sign_up=listchoosed=>{
    return dispatch=>{
        dispatch(listSelect(listchoosed))
    }
}
//发布活动
export const publishlist=activity=>{
    return async dispatch=>{
        const responese=await Published(activity)
        const {code}=responese.data;
        const {data}=responese;
        if(code==1)
        {
            dispatch(publishedSuccess(data))
        }
        else{
            dispatch(publishederror(data))
        }
    }
}
export const pclogin=user=>{
    return async dispatch=>{
        const responese=await Pclogin(user)
        const {code}=responese.data;
        const {data}=responese;
        if(code==1)
        {
            dispatch(loginSuccess(data))
        }
        else{
            dispatch(Loginfail(data))
        }
    }
}