import React, { Component } from 'react';
import '../../assets/less/head.less';
import logo from '../../assets/image/logo.jpg'
class Header extends Component{
    render()
    {
        return(
            <div className="head">
                <div className="title">聚益</div>
              <img src={logo} alt=""/>
            </div>
        )
    }
}
export default Header