import React, { Component } from "react";
import Title from "../../components/header/title";
import { List, Card, WingBlank, WhiteSpace, Grid } from "antd-mobile";
import "../../assets/less/ActiveList.less";
import Iconlist from "../../assets/image/Iconlist.png";
import xianaixin from "../../assets/image/献爱心.png";
import xianaixin1 from "../../assets/image/献爱心(1).png";
import fabu from '../../assets/image/发布.png';
import fabu1 from '../../assets/image/发布 (1).png';
import lihe from "../../assets/image/礼盒.png";
import lihe1 from "../../assets/image/礼盒(1).png";
import geren from "../../assets/image/个人.png";
import geren1 from "../../assets/image/个人(1).png";
import "../../assets/less/main.less";
import { Activelish } from "../../api/index";
import { sign_up } from "../../redux/action";
import { connect } from "react-redux";
let data = [
  {
    icon: xianaixin,
    text: "首页"
  },
  {
    icon: fabu,
    text: "我要发布"
  },
  {
    icon: lihe,
    text: "心愿之池"
  },
  {
    icon: geren,
    text: "个人中心"
  }
];
const Item = List.Item;
const Brief = Item.Brief;
let result = [];
class Publishlist extends Component {
  state = {
    data: "",
    mark: [false, false, true, false],
    listmark: 0
  };
  handleClick(item)
  {
    //console.log(item)
   this.props.sign_up(item);
   this.props.history.push('/sign');
  }
  componentDidMount = async () => {
    const response = await Activelish();
    const { data } = response;
    result = data.data;
    this.setState(() => ({
      data: result
    }));
   // console.log(this.props)
  };
  render() {
    const { mark } = this.state;
    const activities = this.state.data;
    let mark1 = mark[0];
    let mark2 = mark[1];
    let mark3 = mark[2];
    let mark4 = mark[3];
    if (mark1) {
      data[0].icon = xianaixin1;
    } else {
      data[0].icon = xianaixin;
    }
    if (mark2) {
      data[1].icon = fabu1;
    } else {
      data[1].icon = fabu;
    }
    if (mark3) {
      data[2].icon = lihe1;
    } else {
      data[2].icon = lihe;
    }
    if (mark4) {
      data[3].icon = geren1;
    } else {
      data[3].icon = geren;
    }
    return (
      <div style={{ overflowX: "hidden" }} className="container">
        <Title headtitle="心愿之池" back={this.props.history} />
        <List style={{ marginTop: "3.5rem" }} className="my-list">
          {activities ? (
            activities.map((item, index) => {
              return (
                <Item
                  arrow="horizontal"
                  thumb={Iconlist}
                  multipleLine
                  onClick={() => {}}
                  className="am-list-item1"
                  key={item.activity_id}
                  onClick={this.handleClick.bind(this,item)}
                >
                  {item.title} <Brief>{item.subtitle}</Brief>
                </Item>
              );
            })
          ) : (
            <div
              className="am-list-item1"
              style={{
                color: "rgb(136, 136, 136)",
                textAlign: "center",
                marginTop: "3.5rem"
              }}
            >
              没有更多数据了
            </div>
          )}
        </List>
        <div className="bottom">
          <Grid
            data={data}
            activeClassName="active"
            itemStyle={{ height: "3.6rem" }}
            onClick={e => {
              // console.log(e.text)
              if (e.text === "首页") {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [true, false, false, false]
                });
                this.props.history.replace("/pulishmain");
              } else if (e.text === "我要发布") {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [false, true, false, false]
                });
                this.props.history.replace("/publishreceive");
              } else if (e.text === "心愿之池") {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [false, false, true, false]
                });
                this.props.history.replace("/publishlist");
              } else {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [false, false, false, true]
                });
                this.props.history.replace("/publishperson");
              }
            }}
          />
        </div>
      </div>
    );
  }
}
export default connect(
 ()=>{
  return {

  } 
 },
  { sign_up }
)(Publishlist);
