import React, { Component } from 'react';
import Title from "../../components/header/title";
import '../../assets/less/credit.less';
import {connect} from 'react-redux';
class Credit extends Component{
    render()
    {
        console.log(this.props.user.user.user.credit);
        const credit=this.props.user.user.user.credit
        return(
            <div className="credit">
               <Title headtitle="信用积分" back={this.props.history} />
               <div className="creditbackground">
                 <div>{credit ?credit:0}</div>
               </div>
            </div>
        )
    }
}
export default connect(
    state=>{
        return{
        user:state
        }
    },{}
)(Credit);