import React, { Component } from "react";
import Title from "../../components/header/title";
import { Grid } from "antd-mobile";
import xianaixin from "../../assets/image/献爱心.png";
import xianaixin1 from "../../assets/image/献爱心(1).png";
import jieshou from "../../assets/image/接收.png";
import jieshou1 from "../../assets/image/接收(1).png";
import lihe from "../../assets/image/礼盒.png";
import lihe1 from "../../assets/image/礼盒(1).png";
import geren from "../../assets/image/个人.png";
import geren1 from "../../assets/image/个人(1).png";
import ren from "../../assets/image/人.png";
import "../../assets/less/main.less";
import "../../assets/less/receive.less";
import { Receive,Addreceiver } from "../../api/index";
import { connect } from "react-redux";
import moment from 'moment'
let data = [
  {
    icon: xianaixin,
    text: "首页"
  },
  {
    icon: jieshou,
    text: "我的接受"
  },
  {
    icon: lihe,
    text: "心愿之池"
  },
  {
    icon: geren,
    text: "个人中心"
  }
];
class Receivelist extends Component {
  state = {
    mark: [false, true, false, false],
    datas: []
  };
  async componentDidMount() {
    console.log(this.props)
    const phone = this.props.user.user.user.phone;
    const response = await Receive({ phone });
    this.setState({
      datas: response.data.data
    });
    console.log(response);
  }
  handleclick=async (title)=>{
   const phone = this.props.user.user.user.phone;
   const finish={
       title:title,
       finishtime:moment().format(),
       phone
   }
   const response =await Addreceiver(finish);
   this.setState({
    datas: response.data.data
  });
  }
  componentDidUpdate()
  {
    console.log('asdas');
  }
  render() {
    console.log(moment().format());
    const { mark } = this.state;
    let mark1 = mark[0];
    let mark2 = mark[1];
    let mark3 = mark[2];
    let mark4 = mark[3];
    if (mark1) {
      data[0].icon = xianaixin1;
    } else {
      data[0].icon = xianaixin;
    }
    if (mark2) {
      data[1].icon = jieshou1;
    } else {
      data[1].icon = jieshou;
    }
    if (mark3) {
      data[2].icon = lihe1;
    } else {
      data[2].icon = lihe;
    }
    if (mark4) {
      data[3].icon = geren1;
    } else {
      data[3].icon = geren;
    }
    console.log(this.state);
    const { datas } = this.state;
    return (
      <div style={{ }}>
        <Title headtitle="我的接收" back={this.props.history} />
        <div className="recevelist">
          {datas.map((item, index) => {
            return (
              <div key={index}>
                <div className="receive-head">
                  <img src={ren} alt="" />
                  <div>发布者:{item.person}</div>
                  <button onClick={this.handleclick.bind(this,item.title)}>我已完成</button>
                </div>
                <div className="receive-introduce">
                  <ul>
                    <li className="receive-title">
                      志愿类型：<span>{item.title}</span>
                    </li>
                    <li className="receive-body"> {item.introduce}</li>
                  </ul>
                  <div className="geli"></div>
                </div>
              </div>
            );
          })}
        </div>
        <div className="bottom">
          <Grid
            data={data}
            activeClassName="active"
            itemStyle={{ height: "3.6rem" }}
            onClick={e => {
              // console.log(e.text)
              if (e.text === "首页") {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [true, false, false, false]
                });
                this.props.history.push("/");
              } else if (e.text === "我的接受") {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [false, true, false, false]
                });
                this.props.history.push("/receive");
              } else if (e.text === "心愿之池") {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [false, false, true, false]
                });
                this.props.history.push("/list");
              } else {
                this.setState({
                  mark: [false, false, false, false]
                });
                this.setState({
                  mark: [false, false, false, true]
                });
                this.props.history.push("/person");
              }
            }}
          />
        </div>
      </div>
    );
  }
}
export default connect(
  state => ({
    user: state
  }),
  {}
)(Receivelist);
