import React, { Component } from "react";
import "../../../assets/less/pclogin.less";
import logo from "../../../assets/image/logo.jpg";
import { Form, Icon, Input, Button, message } from "antd";
import {pclogin} from '../../../redux/action';
import {connect} from 'react-redux';
class Pclogin extends Component {
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            this.props.pclogin(values)
          }
        });
      };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { user } = this.props.user;
    const { status, Redirecto } = user;
    
    if (status === 1) {
       this.props.history.replace('/pcmain');
    }
    return (
      <div className="contain">
        <h1>聚益志愿服务系统</h1>
        <div className="pclogin-form">
          <div className="pclogin-login">
            <Form onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator("username", {
                  rules: [
                    { required: true, message: "Please input your username!" }
                  ]
                })(
                  <Input
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder="账号"
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("password", {
                  rules: [
                    { required: true, message: "Please input your Password!" }
                  ]
                })(
                  <Input
                    prefix={
                      <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    type="password"
                    placeholder="密码"
                  />
                )}
              </Form.Item>
              <Form.Item>
                
                <Button
                  type="primary"
                  htmlType="submit"
                  style={{width:"100%"}}
                >
                  登录
                </Button>
                
              </Form.Item>
            </Form>
          </div>
          <div className="pclogin-image">
              <div className="background">
                 <img src={logo} alt=""/>
                 <h6>聚益志愿服务系统</h6>
              </div>
          </div>
        </div>
      </div>
    );
  }
}
const Formpclogin = Form.create()(Pclogin);
export default connect(
    state=>{
        console.log(state.user)
        const {user}=state;
        const {status,msg}=user;
        if(status===1)
        {
            message.success('登录成功',1);
        }
        if(status===0&&msg)
        {
            message.error('登录失败',1);
        }
        return{
         user:state
        }
    },
    {pclogin}
)(Formpclogin);
