import {combineReducers} from 'redux';
import {
    SuCCESS_MESSAGE, FAIL_MESSAGE, SUCCESS_REGISTER, INFOR_FAIL, INFOR_SUCCESS
    , LIST_CHOOSE, PUBLISG_SUCCESS, PUBLISH_FAIL, LOGIN_SUCCESS, LOGIN_FAIL,INFOR_FAIL1,INFOR_SUCCESS1
} from './actionType'
const userState = {
    status: '',
    msg: '',
    Redirecto: '',
    user: ''
}
const listSate = {
    activity_id: '',
    address: '',
    introduce: '',
    organization: '',
    person: '',
    pulishtime: '',
    reciver: '',
    stoptime: '',
    subtitle: '',
    telephone: '',
    title: '',
    volunteertime: ''
}
const initpublish = {
    code: '',
    message: ' '
}

function user(state = userState, action) {
    if (action.type === SuCCESS_MESSAGE) {
        const newState = JSON.parse(JSON.stringify(userState));
        newState.status = action.data.code;
        newState.Redirecto = '/';
        newState.user = action.data.user;
        console.log(action.data)
        return newState
    }
    else if (action.type === FAIL_MESSAGE) {
        const newState = JSON.parse(JSON.stringify(userState));
        newState.msg = action.data.msg;
        newState.status = action.data.code;
        return newState
    }
    else if (action.type === SUCCESS_REGISTER) {
        const newState = JSON.parse(JSON.stringify(userState));
        newState.status = action.data.code;
        newState.Redirecto = '/login';
        return newState
    }
    else if (action.type === LOGIN_SUCCESS) {
        const newState = JSON.parse(JSON.stringify(userState));
        newState.status = action.data.code;
        newState.msg = action.data.msg;
        newState.Redirecto = '/pcmain';
        return newState
    }
    else if (action.type === LOGIN_FAIL) {
        const newState = JSON.parse(JSON.stringify(userState));
        newState.status = action.data.code;
        newState.msg = action.data.msg;
        return newState
    }
    // else if (action.type === INFOR_FAIL) {
       
    //         const newState = JSON.parse(JSON.stringify(action.data));
    //         //console.log(action.data);
    //         return newState
       
    // }
    // else if(action.type===INFOR_SUCCESS)
    // {
    //     const newState = JSON.parse(JSON.stringify(action.data));
    //     //console.log(action.data);
    //     return newState
    // }
    return state
}
function infor(state={},action)
{
    if (action.type === INFOR_FAIL) {
       
            const newState = JSON.parse(JSON.stringify(action.data));
            console.log(action.data);
            return newState
       
    }
    else if(action.type===INFOR_SUCCESS)
    {
        const newState = JSON.parse(JSON.stringify(action.data));
        console.log(action.data);
        return newState
    }
    else if(action.type===INFOR_SUCCESS1)
    {
        const newState = JSON.parse(JSON.stringify(action.data));
        //console.log(action.data);
        return newState
    }
    else if(action.type===INFOR_FAIL1)
    {
        const newState = JSON.parse(JSON.stringify(action.data));
        //console.log(action.data);
        return newState
    }
    return state
}
function list(state = listSate, action) {
    if (action.type === LIST_CHOOSE) {
        const newState = JSON.parse(JSON.stringify(action.data));
        //console.log(action.data);
        return newState
    }
    return state
}
function publish(state = initpublish, action) {
    if (action.type === PUBLISG_SUCCESS) {
        const newState = JSON.parse(JSON.stringify(action.data));
        return newState
    }
    return state;
}
//pc
export default combineReducers({
    user, list, publish,infor
})