import React, { Component } from "react";
import Title from "../../components/header/title";
import "../../assets/less/heart.less";
import jiangpai from "../../assets/image/奖牌.png";
import { Heartlist } from "../../api/index";
import student from "../../assets/image/学生.png";
import {connect} from 'react-redux'
class Heart extends Component {
  state = {
    datas: ""
  };
  async componentDidMount() {
    var infor={};
    infor.phone=this.props.user.user.user.phone;
    const response = await Heartlist(infor);
    const { data } = response;
    //console.log(data.data);
    this.setState({
      datas: data.data
    });
  }
  render() {
    const { datas } = this.state;
    const heartlist = Array.from(datas);
    console.log(datas);
    return (
      <div style={{ width: "100vw", height: "100vh", overflowX: "hidden" }}>
        <Title headtitle="爱心榜" back={this.props.history} />
        <div className="heartbackground">
          <ul>
            <li className="hearttitle">排行榜</li>
            <li className="heartimg">
              <img src={jiangpai} alt="" />
            </li>
          </ul>
        </div>
        <div className="heartrank">
          {heartlist.map((item, index) => {
           
            return (
            
              <div className="rankitem" key={index}>
                <ul>
            <li className="itemhead">{index}</li>
                  <img src={student} alt="" />
            <li className="itemperson">{item.phone.trim()}</li>
            <li className="itemtime">{item.volunteertime.substring(0,2)}时{item.volunteertime.substring(3,5)}分{item.volunteertime.substring(6,8)}秒</li>
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}
export default connect(state=>({user:state}),{})(Heart);
